.section {
    display: block;
    width: 500px;
    margin: 0 auto;
    margin-top: 20px;
    padding: 20px;
    background-color: rgb(207, 207, 220);
    border: 2px solid gray;
    border-radius: 8px;
    box-shadow: 0px 3px 5px 0px #000000;
}

.title {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 20px;
}

.label {
    display: flex;
    flex-direction: column;
    font-size: 16px;
    font-weight: bold;
}

.input {
    width: 480px;
    height: 20px;
    margin-top: 10px;
    margin-bottom: 20px;
    padding-left: 10px;
    border: 2px solid gray;
    border-radius: 8px;
    outline: none;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
    transition: all 250ms linear;
}

.input:hover,
.input:focus {
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.75);
    transition: all 250ms linear;
}

.button {
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
    width: 100px;
    margin: 0 auto;
    padding: 5px;
    cursor: pointer;
    text-decoration: none;
    border-radius: 8px;
    border: 2px solid rgb(0, 0, 0);
    background-color: rgb(175, 185, 195);
    font-size: 14px;
    font-weight: bold;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
    transition: all 250ms linear;
}

.button:hover,
.button:focus {
    border: 2px solid rgb(10, 12, 12);
        background-color: rgb(50, 113, 181);
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.75);
    transition: all 250ms linear;
    color: white;
}